import { FETCH_POSTS } from 'action-types';
import commonActions from 'actions/common.actions';



export const getSpaceEx = () => dispatch => {
  dispatch(commonActions.setLoader(true));
  const url = `https://api.spacexdata.com/v3/launches?limit=100`;
  fetch(url)
    .then(res => res.json()).then((data) => {
      dispatch(commonActions.setLoader(false));
      dispatch({
        type: FETCH_POSTS,
        payload: data
      })
    }
    );

};

export const getSpaceExFilteredData = (e, FilteredData, records, isYearPresent = false, filterSelected = false) => dispatch => {
  e && e.preventDefault();
  let url, anchorList;
  dispatch(commonActions.setLoader(true));
  if (filterSelected) {
    var elems = document.querySelectorAll(".filterBoxWrap a");

    [].forEach.call(elems, function (el) {
      el.classList.remove("active");
    });
    e.target.classList.add("active");
  }

  if (isYearPresent) {
    const year = e.target.getAttribute("data-year");
    url = `https://api.spacexdata.com/v3/launches?limit=${records}${FilteredData}${year}`;
  }
  else {
    url = `https://api.spacexdata.com/v3/launches?limit=${records}${FilteredData}`;
  }
  fetch(url)
    .then(res => res.json()).then((data) => {
      dispatch(commonActions.setLoader(false));
      dispatch({
        type: FETCH_POSTS,
        payload: data
      })
    }
    );

};


