import HeaderMenu from 'components/atoms/header-menu';
import PropTypes from 'prop-types';
import React, { Component } from 'react';

class Posts extends Component {
    componentDidMount() {
        this.props.getSpaceEx();
        console.log('props', this.props)
    }

    render() {
        const { getSpaceExFilteredData, spaceExData } = this.props
        const postItems = spaceExData.map((rocketData, i) => (
            <a href={rocketData.links.article_link} className="box" key={i} target="_blank">
                <img src={rocketData.links.mission_patch_small} alt="" width="150" />
                <strong>{rocketData.mission_name} #{rocketData.flight_number}</strong>
                <div>
                    <strong className="detailHd">Mission Ids:</strong>

                    {rocketData.mission_id.length > 0 ? <ul>{rocketData.mission_id.map((missionId, i) => (
                        <li key={i}>{missionId || 'Not Available'}</li>
                    ))}
                    </ul> : '-'}
                </div>
                <strong className="detailHd">Launch Year:</strong>
                <p>{rocketData.launch_year}</p>
                <strong className="detailHd">Successful Launch:</strong>
                <p>{rocketData.launch_success ? 'true' : 'false'}</p>
                <strong className="detailHd">Successful Landing:</strong>
                <p>{rocketData.rocket.first_stage.cores[0].land_success ? 'true' : rocketData.rocket.first_stage.cores[0].land_success === null ? 'NA' : 'false'}</p>
            </a>
        ));
        return (
            <div>
                <HeaderMenu {...this.props}></HeaderMenu>
                <div className="mainContainer">
                    <h1>SpaceX Launch Programs</h1>
                    <div className="wrapper">
                        <div className="colLeft">
                            <div className="filterCol">
                                Filters
                                    <p>Launch Year</p>
                                <div className="filterBoxWrap">
                                    <div className="filterBox">
                                        <a href="#" data-year="2006" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2006</a>
                                        <a href="#" data-year="2007" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2007</a>
                                        <a href="#" data-year="2008" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2008</a>
                                        <a href="#" data-year="2009" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2009</a>
                                        <a href="#" data-year="2010" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2010</a>
                                        <a href="#" data-year="2011" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2011</a>
                                        <a href="#" data-year="2012" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2012</a>
                                        <a href="#" data-year="2013" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2013</a>
                                        <a href="#" data-year="2014" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2014</a>
                                        <a href="#" data-year="2015" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2015</a>
                                        <a href="#" data-year="2016" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2016</a>
                                        <a href="#" data-year="2017" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2017</a>
                                        <a href="#" data-year="2018" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2018</a>
                                        <a href="#" data-year="2019" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2019</a>
                                        <a href="#" data-year="2020" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true&launch_year=', 100, true, true)}>2020</a>
                                    </div>
                                </div>
                            </div>
                            <div className="filterCol">
                                <p>Successful Launch</p>
                                <div className="filterBoxWrap">
                                    <div className="filterBox">
                                        <a href="#" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true', 100, false, true)}>True</a>
                                        <a href="#" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=false', 100, false, true)}>False</a>
                                    </div>
                                </div>
                            </div>
                            <div className="filterCol">
                                <p>Successful Landing</p>
                                <div className="filterBoxWrap">
                                    <div className="filterBox">
                                        <a href="#" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=true', 100, false, true)}>True</a>
                                        <a href="#" onClick={(e) => getSpaceExFilteredData(e, '&launch_success=true&land_success=false', 100, false, true)}>False</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="colRight">
                            <div className="flexWrap">
                                {postItems && postItems.length ? postItems : 'No Record Found'}
                            </div>
                        </div>
                    </div>

                </div>


            </div>
        );
    }
}

Posts.propTypes = {
    getSpaceEx: PropTypes.func.isRequired,
    spaceExData: PropTypes.array.isRequired,
    newPost: PropTypes.object
};

export default Posts;




